body {
  display: flex;
  flex-direction: column;
}
.container {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  flex: 0 1 auto;
  /* min-height: 100%; */
}
.main-pic-row {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.photos {
  padding: 10px;
}
.description {
  text-align: left;
  line-height: 3em;
  padding-left: 10%;
}
.prop-container {
  margin-top: 20px;
  padding-top: 20px;
  background-color: #f4f4f4;
}
.dtls {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  line-height: 3em;
  text-align: left;
}
#mainPic {
  max-width: 600px;
  height: auto;
}
.subPic {
  max-width: 300px;
  max-height: 300px;
}

@media screen and (max-width: 650px) {
  .description {
    padding-left: 4%;
    padding-right: 4%;
    line-height: 2.2em;
  }
  .prop-container {
    display: flex;
    flex-direction: column;
  }
  .dtls {
    flex-direction: column;
    align-content: center;
    line-height: 2.4em;
    margin-top: 10px;
    margin-left: 16%;
  }
}